import React from "react";
import pic1 from "../assets/HobbyBlog.svg";
import pic2 from "../assets/HobbyCreator.svg";
import pic3 from "../assets/HobbyPhoto.svg";
import pic4 from "../assets/HobbyGames.svg";
import pic5 from "../assets/HobbyFootball.svg";

class MainExperience extends React.Component {
	render() {
		return (
			<div className="Hobby">
				<h1 className="text-center title">My Hobby</h1>
				<div className="hobby-item container">
					<div className="row ">
						<div className="col-6 text-right">
							<img src={pic1} />
						</div>
						<div className="col-6 text-left">
							<h4>Blogging</h4>
						</div>
						<div class="w-100"></div>
						<div className="col-6 text-right ">
							<h4>Content Creator</h4>
						</div>
						<div className="col-6 text-left ">
							<img src={pic2} />
						</div>
						<div class="w-100"></div>
						<div className="col-6 text-right">
							<img src={pic3} />
						</div>
						<div className="col-6 text-left">
							<h4>Photography</h4>
						</div>
						<div class="w-100"></div>
						<div className="col-6 text-right">
							<h4>Playing Games</h4>
						</div>
						<div className="col-6 text-left">
							<img src={pic4} />
						</div>
						<div class="w-100"></div>
						<div className="col-6 text-right">
							<img src={pic5} />
						</div>
						<div className="col-6 text-left">
							<h4>Football</h4>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default MainExperience;
